import AddMask from './child/AddressMask.vue'
export default {
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			maskShow:false,
			address:"",
			bz:""
			
		}
	},
	methods: {
		back() {
			uni.navigateBack();
		},
		addAddress() {
			if(this.address=== '') {
				uni.showToast({
					title:"请输入保存的地址",
					icon:"none"
				})
				return;
			}else if(this.bz=== '') {
				uni.showToast({
					title:"请输入地址备注",
					icon:"none"
				})
				return;
			}
			this.maskShow = !this.maskShow;
		},
		close() {
			this.maskShow = !this.maskShow;
			setTimeout(()=>{
				uni.navigateTo({
					url:'../../../../USDTAddress/USDTAddress'
				})
			},1500)
		}
		
	},
	components: {
		AddMask
	}
}
